*{
    list-style: none;
}

section{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.imgWrapper{
    width: 50%;
    padding: 50px;
}

.infoWrapper{
    display: flex;
    flex-direction: column;
    width:  50%;
}

.infoWrapper h2{
    padding: 10px 20px;
}

.imgWrapper img{
    width: 100%;
    height: 100%;
    border-radius: 0;
}
.content{
    display: flex;
    justify-content: space-between;
    padding: 20px;
}

.borders{
    display: flex;
    align-items: center;
    padding: 20px;
}

.borderWrapper{
    background-color: rgb(238, 238, 238);
    margin: 5px;
    border: 1px solid grey;
    border-radius: 4px;
    padding: 5px;
    width: 50px;
    text-align: center;
}

.section-dark-mode{
    background-color: hsl(207, 26%, 17%);
    color: #fff;
}

.section-dark-mode .borderWrapper{
    background-color: hsl(209, 23%, 22%);
    border: 1px solid rgb(48, 48, 48);
}

.container-dark-mode{
    background-color: hsl(207, 26%, 17%);
    height: 100vh;
}
.container-light-mode{
    height: 100vh;
}
.button-dark-mode{
    margin: 30px 40px 0 40px;
    width: 100px;
    padding: 5px 20px;
    background-color: hsl(209, 23%, 22%);
    color: #fff;
    border: 1px solid rgb(48, 48, 48);
    border-radius: 5px;
    cursor: pointer;
    box-shadow: 0 0 3px rgb(34, 34, 34);
}
.button-light-mode{
    margin: 30px 40px 0 40px;
    width: 100px;
    padding: 5px 20px;
    background-color: rgb(238, 238, 238);
    color: black;
    border: 1px solid rgb(226, 225, 225);
    border-radius: 5px;
    cursor: pointer;
    box-shadow: 0 0 3px rgb(177, 177, 177);
}
button{
    display: flex;
    align-items: center;
    justify-content: center;
}