#navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px 20px;
}

input{
    border: 0;
}
.searchbar{
    display: flex;
    align-items: center;
    width: 300px;
    padding: 10px;
    border-radius: 8px;
    border: 0;
    box-shadow: 0 0 3px #bebebe;
    margin: 20px;
    background-color: #fff;
}

span{
    margin: 0 10px;
    cursor: pointer;
}

select{
    cursor: pointer;
    width: 200px;
    padding: 10px;
    border-radius: 8px;
    border: 0;
    box-shadow: 0 0 3px #bebebe;
    margin: 20px;
}

.navbar-dark-mode .searchbar{
    background-color: hsl(209, 23%, 22%);
    box-shadow: 0 0 3px #3b3b3b;
}

.navbar-dark-mode span{
    background-color: hsl(209, 23%, 22%);
    color: #fff;
}
.navbar-dark-mode input{
    background-color: hsl(209, 23%, 22%);
    color: #fff;
}

.navbar-dark-mode input::placeholder{
    color: #fff;
}
.navbar-dark-mode select{
    background-color: hsl(209, 23%, 22%);
    color: #fff;
    box-shadow: 0 0 3px #3b3b3b;
}