img{
    width: 100%;
    height: 120px;
    border-radius: 10px 10px 0 0;
}

#card{
    margin: 20px;
    width: 200px;
    height: 250px;
    border-radius: 10px;
    cursor: pointer;
}

.text{
    padding: 0 10px 0 10px;
}

h4{
    margin: 8px 0 8px 0;
}

.card-dark-mode{
    background-color: hsl(209, 23%, 22%);
    color: #fff;
}